.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #CC653C;
  border-radius: 34px;

  -webkit-transition: .23s;
  transition: .23s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  border-radius: 50%;

  -webkit-transition: .23s;
  transition: .23s;
}


input:checked + .slider:before {
  -webkit-transform: translateX(26px);
